$bottom: 5.5em;
$nudge: 5px;
$middle: 50%;
$offset-h: -16.5%;
$offset-v: -15px;

.video-js {
	&:not(:hover) {
		.vjs-overlay-bottom-right {
			bottom: #{2 * $nudge};
		}

		.vjs-overlay-bottom {
			bottom: #{2 * $nudge};
		}

		.vjs-overlay-bottom-left {
			bottom: #{2 * $nudge};
		}
	}

	.vjs-overlay {
		color: #fff;
		position: absolute;
		text-align: center;
	}

	.vjs-overlay-no-background {
		max-width: 33%;
	}

	.vjs-overlay-background {
		background-color: rgba(255, 255, 255, 0.4);
		border-radius: round($nudge / 2);
		padding: $nudge * 2;
		width: 33%;
	}

	.vjs-overlay-top-left {
		top: $nudge;
		left: $nudge;
	}

	.vjs-overlay-top {
		left: $middle;
		margin-left: $offset-h;
		top: $nudge;
	}

	.vjs-overlay-top-right {
		right: $nudge;
		top: $nudge;
	}

	.vjs-overlay-right {
		right: $nudge;
		top: $middle;
		transform: translateY(-50%);
	}

	.vjs-overlay-bottom-right {
		bottom: $bottom;
		right: $nudge;
	}

	.vjs-overlay-bottom {
		bottom: $bottom;
		left: $middle;
		margin-left: $offset-h;
	}

	.vjs-overlay-bottom-left {
		bottom: $bottom;
		left: $nudge;
	}

	.vjs-overlay-left {
		left: $nudge;
		top: $middle;
		transform: translateY(-50%);
	}

	.vjs-overlay-center {
		left: $middle;
		margin-left: $offset-h;
		top: $middle;
		transform: translateY(-50%);
	}
}
